import './index.scss';
import { ErrorUtil } from '../../core/utils/error-util';
import PropTypes from 'prop-types';
import React from 'react';

/**
 * Page qui sert  afficher les messages d'rreurs dans les cas suivants :
 * - Serveur hors ligne
 * - Page non autoris (message non implmenter actuellement)
 * - Session expir (message non implmenter actuellement)
 * @param message
 * @return {Element}
 * @constructor
 */
const ErrorPage = ({ message }) => {
  return (
    <>
      {message === ErrorUtil.messageNotFound ? (
        <>
          <h1>{message}</h1>
          <p>Possibilits : </p>
          <ul className={'case-error'}>
            <li>L'utilisateur n'existe pas.</li>
            <li>J'ai remplac les endpoints de l'API (voir documentation).</li>
          </ul>
        </>
      ) : (
        <h1>{message}</h1>
      )}
    </>
  );
};

ErrorPage.propTypes = {
  message: PropTypes.string.isRequired,
};

export default ErrorPage;
